import React, { Component } from "react";
import axios from "axios";

class Signup extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fullName: "",
      email: "",
      originalPassword: "",
      currentUser: null
    }
  }

  genericSync(event) {
    const { name, value } = event.target;

    this.setState({[name]: value});
  }

  handleSubmit(event) {
    event.preventDefault();

    axios.post(process.env.REACT_APP_SERVER_URL + "/api/signup", this.state)
    .then(response => {
      console.log("Signup page", response.data);
      const { userDoc } = response.data;
      // send "userDoc" to App.js function that changes "currentUser"
      this.props.onUserChange(userDoc);
    })
    .catch(err => {
      console.log("Something went wrong", err);
      alert("No sign up possible ! Try again.")
    });
  }

  render() {
    // check currentUser (received from App.js)
    if (this.props.currentUser) {
      return (
        <section>
          <h2>You are signed up !</h2>
          <o>
            Welcome, {this.props.currentUser.fullName}!
            Your user ID is <b>{this.props.currentUser._id}</b>
          </o>
        </section>
      )
    }

    return(
      <section>
        <h2>Sign Up</h2>

        <form onSubmit={event => this.handleSubmit(event)}>
          <label>
            Full Name: <input onChange={event => this.genericSync(event)} value={this.state.fullName} type="text" name="fullName" placeholder="Roger" />
          </label>

          <label>
            Email: <input onChange={event => this.genericSync(event)} value={this.state.email} type="email" name="email" placeholder="Roger@rotisserie.com" />
          </label>

          <label>
            Password: <input onChange={event => this.genericSync(event)} value={this.state.originalPassword} type="password" name="originalPassword" placeholder="E'l'donne pas Ã  ta gonz" />
          </label>

          <button>Sign Up</button>
        </form>
      </section>
    )
  }
}

export default Signup;