import React, { Component } from "react";
import axios from "axios";
import { Redirect } from "react-router-dom";

class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: "",
      originalPassword: "",
      currentUser: null,
    }
  }

  genericSync(event) {
    const { name, value } = event.target;

    this.setState({[name]: value});
  }

  handleSubmit(event) {
    event.preventDefault();

    axios.post(process.env.REACT_APP_SERVER_URL + "/api/login", this.state, { withCredentials: true })
    .then(response => {
      const { userDoc } = response.data;
      // send "userDoc" to App.js function that changes "currentUser"
      this.props.onUserChange(userDoc);
    })
    .catch(err => {
      console.log("Something went wrong", err)
    })
  }

  render() {
    // check currentUser (received from App.js)
    if (this.props.currentUser) {
      return <Redirect to="/phone-list" />
    }

    return(
      <section>
        <h2>Log In</h2>

        <form onSubmit={event => this.handleSubmit(event)}>
          <label>
            Email: <input onChange={event => this.genericSync(event)} value={this.state.email} type="email" name="email" placeholder="Roger@rotisserie.com" />
          </label>

          <label>
            Password: <input onChange={event => this.genericSync(event)} value={this.state.originalPassword} type="password" name="originalPassword" placeholder="E'l'donne pas Ã  ta gonz" />
          </label>

          <button>Log In</button>
        </form>
      </section>
    )
  }
}

export default Login;